import token from '../../components/token.vue';
import {
	mainSlider
} from '../../common/js/json.js';
import {
	mapState
} from 'vuex';

export default {
	data() {
		return {
			slides: '',
			indicatorDots: false,
			autoplay: false,
			interval: 2000,
			duration: 500,
			circular: false,
			currentSwiper: 0,
			tokens: []
		}
	},
	components: {
		token
	},
	methods: {
		toPan() {
			uni.switchTab({
				url: '../pan/pan'
			});
		},
		getMainSlider() {
			mainSlider().then(data => {
				this.slides = data;
			});
		},
		async getToken() { //获取token
			let res = await this.api.homeToken().getIndexPan();
			if (res.data.status == 200) {
				this.tokens = res.data.data;
			}
		},
		pushToken(item) { //收取token
			this.tokens.splice(item.index, 1);
			this.takePan(item.token);
		},
		async takePan(token) {
			let res = await this.api.homeToken().takePan({
				numbers: Number(token)
			});
			if (res.data.status == 200) {
				console.log("成功");
			}
		}
	},
	onLoad() {
		this.getMainSlider();
		this.getToken();
	}
}
